import axios from "../../node_modules/axios/index";
var getRequest1 = document.getElementById("getRequest1");
getRequest1.addEventListener("click", performGetRequest1);
var getRequest2 = document.getElementById("getRequest2");
getRequest2.addEventListener("click", performGetRequest2);
var post = document.getElementById("post");
post.addEventListener("submit", performPostRequest);
var clear = document.getElementById("clear");
clear.addEventListener("click", clearResult);
var clear1 = document.getElementById("clear1");
clear1.addEventListener("click", clearResult);
var clear2 = document.getElementById("clear2");
clear2.addEventListener("click", clearResult);
function clearResult() {
    var resultElement = document.getElementById('getResult1');
    var resultElement2 = document.getElementById('getResult2');
    var resultElementPost = document.getElementById('postResult');
    resultElement.innerHTML = '';
    resultElement2.innerHTML = '';
    resultElementPost.innerHTML = '';
}
function performGetRequest1() {
    var resultElement = document.getElementById('getResult1');
    resultElement.innerHTML = '';
    axios.get('https://jsonplaceholder.typicode.com/todos')
        .then(function (response) {
        resultElement.innerHTML = JSON.stringify(response.data);
    })
        .catch(function (error) {
        resultElement.innerHTML = error.message;
    });
}
function performGetRequest2() {
    var resultElement = document.getElementById('getResult2');
    resultElement.innerHTML = '';
    var todoId = document.getElementById('todoId');
    var todoIdValue = todoId.value;
    axios.get('https://jsonplaceholder.typicode.com/todos', {
        params: {
            id: todoIdValue
        }
    })
        .then(function (response) {
        resultElement.innerHTML = JSON.stringify(response.data);
    })
        .catch(function (error) {
        resultElement.innerHTML = error.message;
    });
}
function performPostRequest() {
    var resultElement = document.getElementById('postResult');
    var todoTitle = document.getElementById('todoTitle');
    resultElement.innerHTML = '';
    var todoTitleValue = todoTitle.value;
    axios.post('https://jsonplaceholder.typicode.com/todos', {
        userId: '1',
        title: todoTitleValue,
        completed: false
    })
        .then(function (response) {
        resultElement.innerHTML = JSON.stringify(response.data);
    })
        .catch(function (error) {
        resultElement.innerHTML = error.message;
    });
}
